body,
html {
  background-color: #eeeeee;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
}
.clear:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}
.clear {
  zoom: 1;
}
.pos-r {
  position: relative;
}
.hide {
  display: none;
}
a,
a:hover {
  text-decoration: none;
}
header {
  position: relative;
  padding: 0.4rem;
  background: linear-gradient(to bottom, #1BB0FC 0%, #25C3FA 100%);
}
header h2 {
  text-align: center;
  font-size: 18px;
  color: #fff;
}
[data-dpr="2"] header h2 {
  font-size: 36px;
}
[data-dpr="3"] header h2 {
  font-size: 54px;
}
header .arrow {
  position: absolute;
  top: 50%;
  left: 0.26666667rem;
  width: 0.26666667rem;
  height: 0.26666667rem;
  transform-origin: 50% 50%;
  transform: translateY(-50%) rotate(45deg);
  border-left: 2px solid #fff;
  border-bottom: 2px solid #fff;
}
